html {
  font-size: 62.5%; }

body {
  box-sizing: border-box;
  padding: 3rem; }

.container {
  margin: 0;
  padding: 0; }

h1 {
  font-size: 22px;
  margin: 0;
  font-weight: 400;
  line-height: 20px; }

a {
  text-decoration: none; }

a:hover {
  color: #101010; }

.sidebar__listing__describe {
  font-size: 1.2rem; }

.map {
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 80%;
  height: 100vh; }

/* Marker tweaks */
.mapboxgl-popup-close-button {
  display: visible; }

.mapboxgl-popup-content {
  font: 400 15px/22px 'Source Sans Pro', 'Helvetica Neue', sans-serif;
  padding: 0;
  width: 250px; }

.mapboxgl-popup-content h3 {
  background: #8b9115;
  color: #fff;
  margin: 0;
  padding: 10px;
  border-radius: 3px 3px 0 0;
  font-weight: 700; }

.mapboxgl-popup-content h4 {
  margin: 0;
  padding: 10px;
  font-weight: 400; }

.mapboxgl-popup-content div {
  padding: 10px; }

.mapboxgl-popup-anchor-top > .mapboxgl-popup-content {
  margin-top: 15px; }

.mapboxgl-popup-anchor-top > .mapboxgl-popup-tip {
  border-bottom-color: #91c949; }

.marker {
  border: none;
  cursor: pointer;
  height: 56px;
  width: 56px;
  background-image: url(../img/marker.png); }

.spinner-background {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: white;
  position: absolute; }

.spinner-show {
  visibility: visible;
  width: 80px;
  height: 80px;
  z-index: 10;
  border: 2px solid #f3f3f3;
  border-top: 3px solid #f25a41;
  border-radius: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  animation: spin 1s infinite linear; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

header {
  border-bottom: 0.1rem solid rgba(197, 197, 197, 0.3);
  padding-bottom: 1rem; }

.sidebar {
  position: absolute;
  width: 20%;
  height: 100vh;
  top: 0rem;
  left: 0;
  overflow: hidden;
  border-right: 1px solid rgba(0, 0, 0, 0.3); }
  .sidebar__logo {
    padding-top: 1rem; }
  .sidebar__search {
    margin: 1.5rem;
    padding: 1rem;
    width: 90%;
    border: 1px solid #ceea20ec;
    box-shadow: rgba(0, 0, 0, 0.1) 5px 3px 8px;
    font-size: 1rem;
    pointer-events: none; }
  .sidebar__listings {
    height: 60%;
    overflow: auto;
    padding-bottom: 10rem;
    padding-left: 10px;
    border-bottom: 1px solid #a8a8a8; }
    .sidebar__listings__item {
      position: relative;
      border-bottom: 1px solid #eee;
      padding: 10px;
      text-decoration: none; }
      .sidebar__listings__item--click::after {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        content: ' '; }
      .sidebar__listings__item__title {
        display: block;
        color: #8b9115f3;
        font-weight: 700; }
        .sidebar__listings__item__title:hover {
          color: #8b9115f3;
          transition: background-color 0.2s; }
      .sidebar__listings__item.active {
        background-color: #ceea20ec; }
    .sidebar__listings__item:hover {
      background-color: #f4ffb0ec; }
    .sidebar__listings__item:last-child {
      border-bottom: none; }
  .sidebar__sort {
    font-size: 1.6rem;
    color: #a8a8a8; }

header {
  border-bottom: 0.1rem solid rgba(197, 197, 197, 0.3);
  padding-bottom: 1rem; }

footer {
  font-size: 1rem;
  margin: 1.5rem;
  color: #a8a8a8; }

::placeholder {
  color: rgba(0, 0, 0, 0.432);
  font-size: 1rem; }

::-webkit-scrollbar {
  width: 1rem;
  height: 3px;
  border-left: 0;
  box-shadow: 0;
  background: rgba(0, 0, 0, 0.1); }

::-webkit-scrollbar-track {
  background: none; }

::-webkit-scrollbar-thumb {
  background: #8b9115f3;
  border-radius: 0; }

* {
  box-sizing: border-box; }

body {
  color: #404040;
  font: 400 15px/22px 'Source Sans Pro', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased; }
